@-webkit-keyframes rotate-body {
    from {
        -webkit-transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(-360deg);
    }
}

@-ms-keyframes rotate-body {
    from {
        -ms-transform: rotate(0deg);
    }

    to {
        -ms-transform: rotate(-360deg);
    }
}

@keyframes rotate-body {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(-360deg);
    }
}

@-webkit-keyframes head-tilt {
    from {
        -webkit-transform: rotate(0deg);
    }

    25% {
        -webkit-transform: rotate(-0.8deg);
    }

    50% {
        -webkit-transform: rotate(0deg);
    }

    75% {
        -webkit-transform: rotate(0.8deg);
    }

    to {
        -webkit-transform: rotate(0deg);
    }
}

@-ms-keyframes head-tilt {
    from {
        -ms-transform: rotate(0deg);
    }

    25% {
        -ms-transform: rotate(-0.8deg);
    }

    50% {
        -ms-transform: rotate(0deg);
    }

    75% {
        -ms-transform: rotate(0.8deg);
    }

    to {
        -ms-transform: rotate(0deg);
    }
}

@keyframes head-tilt {
    from {
        transform: rotate(0deg);
    }

    25% {
        transform: rotate(-0.8deg);
    }

    50% {
        transform: rotate(0deg);
    }

    75% {
        transform: rotate(0.8deg);
    }

    to {
        transform: rotate(0deg);
    }
}

@-webkit-keyframes move-shadow {
    from {
        -webkit-transform: scaleX(0.97);
    }

    to {
        -webkit-transform: scaleX(1);
    }
}

@-ms-keyframes move-shadow {
    from {
        -ms-transform: scaleX(0.97);
    }

    to {
        -ms-transform: scaleX(1);
    }
}

@keyframes move-shadow {
    from {
        transform: scaleX(0.97);
    }

    to {
        transform: scaleX(1);
    }
}

.bb-8 {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 240px;
    height: 326px;
    margin-bottom: 50px;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.bb-8__head {
    z-index: 4;
    position: absolute;
    width: 140px;
    height: 93px;
    -webkit-animation: head-tilt 0.3s 0.5s linear infinite;
    -ms-animation: head-tilt 0.3s 0.5s linear infinite;
    animation: head-tilt 0.3s 0.5s linear infinite;
}

.bb-8__head__antenna {
    width: 4px;
    height: 20px;
    background-color: #D3D3D3;
    position: absolute;
    top: -18px;
    left: 120px;
    border-radius: 3px;
}

.bb-8__head__antenna--longer {
    width: 4px;
    height: 30px;
    background-color: #D3D3D3;
    position: absolute;
    top: -25px;
    left: 130px;
    border-radius: 3px;
}

.bb-8__head__antenna--longer:before {
    content: "";
    width: 100%;
    height: 40%;
    background-color: #BEBAB5;
    position: absolute;
    bottom: 0;
}

.bb-8__head__top {
    overflow: hidden;
    position: absolute;
    width: 140px;
    height: 85px;
    background-color: #FFF3E7;
    border-radius: 200px 200px 0 0;
    position: absolute;
    left: 50px;
}

.bb-8__head__top__bar--gray {
    width: 100%;
    height: 10px;
    background-color: #D3D3D3;
    position: absolute;
    top: 10px;
}

.bb-8__head__top__bar--red {
    width: 100%;
    height: 6px;
    background-color: #FF9649;
    position: absolute;
    top: 30px;
}

.bb-8__head__top__bar--red--lower--left {
    width: 18%;
    height: 10px;
    background-color: #FF9649;
    position: absolute;
    left: 0;
    bottom: 15px;
}

.bb-8__head__top__bar--red--lower--right {
    width: 28%;
    height: 10px;
    background-color: #FF9649;
    position: absolute;
    right: 0;
    bottom: 15px;
}

.bb-8__head__top__bar--red--lower--right:before {
    content: "";
    background-color: #FFF3E7;
    width: 6px;
    height: 12px;
    position: absolute;
    left: 10px;
    top: -1px;
}

.bb-8__head__top__bar--gray--lower {
    background-color: #D3D3D3;
    width: 100%;
    height: 10px;
    position: absolute;
    bottom: 0px;
}

.bb-8__head__top__lens {
    width: 35px;
    height: 35px;
    background-color: #555555;
    border: 3px solid #FFF3E7;
    border-radius: 50%;
    position: absolute;
    top: 22px;
    left: 32px;
}

.bb-8__head__top__lens:before {
    content: "";
    width: 8px;
    height: 8px;
    background-color: #C7C5C6;
    border-radius: 50%;
    position: absolute;
    right: 5px;
    top: 7px;
    z-index: 1;
}

.bb-8__head__top__lens__inner {
    width: 25px;
    height: 25px;
    background-color: #414141;
    border-radius: 50%;
    position: absolute;
    top: 5px;
    left: 5px;
}

.bb-8__head__top__lens--secondary {
    width: 12px;
    height: 12px;
    border: 2px solid #414141;
    border-radius: 50%;
    position: absolute;
    right: 50px;
    bottom: 18px;
}

.bb-8__head__top__lens--secondary__inner {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #414141;
    position: absolute;
    top: 2px;
    left: 2px;
}

.bb-8__head__joint {
    width: 138px;
    height: 8px;
    position: absolute;
    top: 85px;
    left: 51px;
    background-color: #BEBAB5;
    z-index: 4;
}

.bb-8__head__joint:before {
    content: "";
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 8px 0 0 8px;
    border-color: transparent transparent transparent #7C9A9C;
    position: absolute;
    top: 1px;
    left: 0;
}

.bb-8__head__joint:after {
    content: "";
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0 8px 8px;
    border-color: transparent transparent #7C9A9C transparent;
    position: absolute;
    top: 1px;
    right: 0;
}

.bb-8__body,
.bb-8__head-shadow {
    width: 240px;
    height: 240px;
    border-radius: 50%;
    overflow: hidden;
    position: absolute;
    top: 85px;
}

.bb-8__body {
    background-color: #FFF3E7;
    z-index: 2;
    -webkit-animation: rotate-body 0.9s 0.5s linear infinite;
    -ms-animation: rotate-body 0.9s 0.5s linear infinite;
    animation: rotate-body 0.9s 0.5s linear infinite;
}

.bb-8__head-shadow {
    background-color: transparent;
    z-index: 3;
    -webkit-animation: head-tilt 0.3s 0.5s linear infinite;
    -ms-animation: head-tilt 0.3s 0.5s linear infinite;
    animation: head-tilt 0.3s 0.5s linear infinite;
}

.bb-8__head-shadow:before {
    content: "";
    width: 100%;
    height: 30px;
    background-color: transparent;
    background-color: rgba(0, 0, 0, 0.2);
    position: absolute;
    top: -8px;
}

.bb-8__body__circle {
    border-radius: 50%;
    width: 120px;
    height: 120px;
    border: 20px solid #FF9649;
    position: absolute;
    z-index: 1;
}

.bb-8__body__circle__bar {
    width: 35px;
    height: 20px;
    background-color: #FF9649;
    position: absolute;
    top: 50px;
    left: 80px;
}

.bb-8__body__circle--one {
    top: -20px;
    left: -40px;
}

.bb-8__body__circle--one__bar--one {
    -webkit-transform: rotate(29deg);
    -ms-transform: rotate(29deg);
    transform: rotate(29deg);
    top: 72px;
}

.bb-8__body__circle--one__bar--two {
    -webkit-transform: rotate(110deg);
    -ms-transform: rotate(110deg);
    transform: rotate(110deg);
    left: 5px;
