}

.bb-8__body__circle--one__inner-circle {
    width: 70px;
    height: 70px;
    background-color: #D3D3D3;
    position: absolute;
    top: 20px;
    left: 10px;
    border-radius: 50%;
}

.bb-8__body__circle--one__inner-circle:before {
    content: "";
    width: 80px;
    height: 12px;
    border-top: 3px solid #FFF3E7;
    border-bottom: 3px solid #FFF3E7;
    position: absolute;
    top: 10px;
    -webkit-transform: rotate(30deg);
    -ms-transform: rotate(30deg);
    transform: rotate(30deg);
}

.bb-8__body__circle--one__inner-circle:after {
    content: "";
    width: 65px;
    height: 12px;
    border-top: 3px solid #FFF3E7;
    border-bottom: 3px solid #FFF3E7;
    position: absolute;
    bottom: 10px;
    -webkit-transform: rotate(30deg);
    -ms-transform: rotate(30deg);
    transform: rotate(30deg);
}

.bb-8__body__circle--one__inner-border {
    width: 70px;
    height: 70px;
    border: 10px solid #D3D3D3;
    border-color: #D3D3D3 transparent transparent transparent;
    position: absolute;
    top: 0px;
    left: 10px;
    border-radius: 50%;
    -webkit-transform: rotate(40deg);
    -ms-transform: rotate(40deg);
    transform: rotate(40deg);
}

.bb-8__body__circle--two {
    top: -20px;
    right: -80px;
}

.bb-8__body__circle--two__bar--one {
    -webkit-transform: rotate(-30deg);
    -ms-transform: rotate(-30deg);
    transform: rotate(-30deg);
