    border: 15px solid #D3D3D3;
    border-color: transparent transparent transparent #D3D3D3;
    position: absolute;
    top: 8px;
    left: 5px;
    border-radius: 50%;
    -webkit-transform: rotate(35deg);
    -ms-transform: rotate(35deg);
